@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  visibility: hidden;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.circle {
  width: 75px;
  height: 75px;
  border-radius: 100%;
  background-color: #fdcf51;
  margin: 50px;
}

.circle.red {
  background-color: #fd5151;
}

.circle.blue {
  background-color: #51a0fd;
}

.link {
  display: block;
  text-decoration: none;
  color: rgb(238, 228, 228);
  margin: 20px auto;
}

.container{width:1440px;height:100vh;margin:0 auto;display:flex;align-items:center}.image-container{width:1440px;height:960px;position:relative;overflow:hidden}.image-container:after{content:"";position:absolute;width:0%;height:100%;top:0;right:0;background-color:white}.image-container img{width:1440px;position:absolute}

